import config from '../../components/config/config.vue'
export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {
		setTimeout(function(){
			var value = uni.getStorageSync('token');
			if(value){
				uni.request({
				    url: config.httpUrl +'/api/v1/user/me',
					method:"GET",
				    header: {
				       'content-type': 'application/x-www-form-urlencoded',
					   'Authorization' : value,
				    },
				    success: (res) => {
						var jsondata=res.data
						if(jsondata.code=='0'){
							uni.switchTab({
							    url: '../show/home'
							});			
						}else{
							config.txtshow('请登录')
							//跳转到登录界面
								uni.redirectTo({
							    url: '../user/login'
							});
						}								
				    },
					fail: () =>{  
						config.txtshow('网络异常')
				     }, 
				});
			}else{
				//跳转到登录界面
				uni.redirectTo({
			    url: '../user/login'
			});
			// uni.switchTab({
			//     url: '../show/home'
			// });	
			}					
			},2000)
	},
	onReady(){
		
	}
}
